import React, { Component, Suspense } from 'react';
import Select from 'react-select';
import CreatableSelect from 'react-select/creatable';
import { Accordion, OverlayTrigger, Tooltip } from 'react-bootstrap';
const ColorPicker = React.lazy(() => import('../../../../CommonComponents/ColorPicker'));

const OPTIONS = [{ value: 1, label: 'Apply Now' },
{ value: 2, label: 'Book Now' },
{ value: 3, label: 'Contact Us' },
{ value: 4, label: 'Download' },
{ value: 5, label: 'Learn More' },
{ value: 6, label: 'Shop Now' },
{ value: 7, label: 'Sign Up' },
{ value: 8, label: 'Donate Now' },
{ value: 9, label: 'Subscribe' },
{ value: 10, label: 'Join Us' },
{ value: 11, label: 'Get Started' },
{ value: 12, label: 'Free Trial' },
{ value: 13, label: 'Get The App' },
{ value: 14, label: 'Watch More' },
{ value: 15, label: 'Get Quote' },
{ value: 16, label: 'Follow Us' },
{ value: 17, label: 'Play Now' }
]
class CTAButton extends Component {
    render() {
        const { cta, onCtaButtonColor, onCtaButtonStatus, onCtaButtonText, onCtaButtonUrl, onCtaTextColor, urlError } = this.props
        let currentIndex = 0;

        if (cta?.buttonText) {
            const findIndex = OPTIONS.findIndex((item) => item.label === cta.buttonText);

            if (findIndex !== -1) {
                currentIndex = findIndex;
            } else {
                currentIndex = OPTIONS.length;
                OPTIONS.push({ value: OPTIONS.length + 1, label: cta.buttonText });
            }
        }

        return <Accordion.Item eventKey="8" className="pb-3">
            <Accordion.Header className="w-100">
                <span className="fs-9 fw-medium">CTA</span>
            </Accordion.Header>
            <Accordion.Body className="border rounded-1 mt-1 p-2">
                <label className="form-check form-switch py-2 align-items-center justify-content-between border-bottom h-40px py-1">
                    <span className="form-check-label ps-0 fs-9 fw-medium text-gray-800">Status</span>
                    <input className="form-check-input" type="checkbox" name="shareIcons" checked={cta.ctaStatus} onChange={onCtaButtonStatus} />
                </label>
                <label className="d-flex align-items-center justify-content-between border-bottom h-40px py-1 position-relative">
                    <span className="ms-0 fs-9 fw-medium">Text Color</span>
                    <Suspense fallback={<></>}>
                        <ColorPicker color={cta.ctaTextColor} onChangeColor={onCtaTextColor} />
                    </Suspense>
                </label>
                <label className="d-flex align-items-center justify-content-between border-bottom h-40px py-1 position-relative">
                    <span className="ms-0 fs-9 fw-medium">Button Color</span>
                    <Suspense fallback={<></>}>
                        <ColorPicker color={cta.ctaButtonColor} onChangeColor={onCtaButtonColor} />
                    </Suspense>
                </label>
                <div className="d-flex align-items-stretch flex-column justify-content-between border-bottom py-1">
                    <label className="form-check form-check-label ps-0 fs-9 fw-medium text-gray-800 my-2" htmlFor='ctaBtnUrl'>Button Text</label>
                    <CreatableSelect key={currentIndex} className="selectpicker mb-3" classNamePrefix="selectpicker" isClearable defaultValue={OPTIONS[currentIndex]} options={OPTIONS} onChange={event => onCtaButtonText(event)} />
                </div>
                <div className="d-flex align-items-stretch flex-column justify-content-between py-1">
                    <label className="form-check form-check-label ps-0 fs-9 fw-medium text-gray-800 my-2" htmlFor='ctaBtnUrl'>Button URL</label>
                    <div className="w-100">
                        <input type="text" className={`form-control form-control-sm w-100 ${urlError ? `is-invalid` : ``}`} id="ctaBtnUrl" placeholder={"https://www.taggbox.com"} onChange={event => onCtaButtonUrl(event)} value={cta.buttonUrl} />
                        {urlError ? <div className="invalid-feedback">Please enter url starting with http:// or https://</div> : null}
                    </div>
                </div>
            </Accordion.Body>
        </Accordion.Item>
    }
}
export default CTAButton